import React from "react";
import ReactDOM from "react-dom";
import bridge from "@vkontakte/vk-bridge";
import { View, Panel, PanelHeader, Div, Button, Cell, Avatar, ScreenSpinner, Header, PanelHeaderBack, InfoRow, Progress, Separator, Counter, Footer } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
bridge.send("VKWebAppInit");

var activePanel = "main";
var panelMain = <ScreenSpinner size="medium" style={{ marginTop: "50%" }}/>;
var panelUserMe = <ScreenSpinner size="medium" style={{ marginTop: "50%" }}/>;
var panelUsers = <ScreenSpinner size="medium" style={{ marginTop: "50%" }}/>;
var staticMenu = [ <Div><Button size="xl" onClick={usersMeInfo}>Моя статистика</Button></Div>, <Div><Button size="xl" onClick={usersInfo}>Участники</Button></Div> ];
var userId = "";
var userLink = "";
var userName = "";
var meStatus = "";
var meBalans = "";
var meActions = "";
var meWinRate = "";
var meWins = "";
var meLose = "";
var meWinRateDop = "";
var meGun = "";
var meWinStrick = "";
var meTime = "";

(async function(){
userId = new URLSearchParams(window.location.search).get("vk_user_id");
var urlVkGet = "https://cors-anywhere.herokuapp.com/https://api.vk.com/method/users.get?user_id=" + userId + "&v=5.107&fields=photo_100&access_token=9d0a4679de9f92b8544f85b68de8bec1d34ff98994f27108ac9183c52997d5a67d9430ca710daacb5c539";
var vkGet = await fetch(urlVkGet, {method: "GET"});
var vkGet = await vkGet.json();
userLink = vkGet.response["0"].photo_100;
userName = vkGet.response["0"].first_name + " " + vkGet.response["0"].last_name;
panelMain = [ <Div><Cell before={<Avatar src={userLink} />} description="Это Вы">{userName}</Cell></Div>, staticMenu ];
ReactDOM.render(<App />, document.getElementById("root"));
}())

async function usersMeInfo() {
activePanel = "userMe";
ReactDOM.render(<App />, document.getElementById("root"));
var urlMeUserGet = "https://cors-anywhere.herokuapp.com/http://f0442357.xsph.ru/botTest/VkApp/userMeInfo.php?user_id=" + userId;
var meUserGet = await fetch(urlMeUserGet, {method: "GET"});
var meUserGet = await meUserGet.json();
meStatus = meUserGet.meStatus;
meBalans = meUserGet.meBalans;
meActions = meUserGet.meActions;
meWinRate = meUserGet.meWinRate;
meWins = meUserGet.meWins;
meLose = meUserGet.meLose;
meWinRateDop = meUserGet.meWinRateDop;
meGun = meUserGet.meGun;
meWinStrick = meUserGet.meWinStrick;
meTime = meUserGet.meTime;
panelUserMe = [ <Div><Cell before={<Avatar src={userLink} />} description={meStatus}>{userName}</Cell><Header>{"Баланс: " + meBalans}</Header><Header>{"Действий: " + meActions}</Header></Div>, <Separator />, <Div><InfoRow header={"Винрейт: " + meWinRate + "%"}><Progress value={meWinRate} /></InfoRow></Div>, <Div style={{display: 'flex'}}><Header indicator={<Counter mode="primary">{meWins}</Counter>}>Побед</Header><Header indicator={<Counter mode="prominent">{meLose}</Counter>}>Поражений</Header></Div>, <Div><Header indicator={<Counter>{meWinRateDop}</Counter>}>Шанс победы</Header><Header indicator={<Counter>{meGun}</Counter>}>Оружие</Header><Header indicator={<Counter>{meWinStrick + " games"}</Counter>}>Winstrick</Header></Div>, <Separator />, <Div><Footer>{"Последнее обновление в " + meTime}</Footer><Button size="xl" onClick={usersMeInfoReload}>Обновить</Button></Div>];
ReactDOM.render(<App />, document.getElementById("root"));
}
function usersMeInfoReload() {
panelUserMe = <ScreenSpinner size="medium" style={{ marginTop: "50%" }}/>;
ReactDOM.render(<App />, document.getElementById("root"));
usersMeInfo();
}
async function usersInfo() {
activePanel = "users";
ReactDOM.render(<App />, document.getElementById("root"));
var urlMeUsersGet = "https://cors-anywhere.herokuapp.com/http://f0442357.xsph.ru/botTest/VkApp/usersInfo.php";
var meUsersGet = await fetch(urlMeUsersGet, {method: "GET"});
var meUsersGet = await meUsersGet.json();
panelUsers = [];
for (var i = 1; i <= Object.keys(meUsersGet).length; i++) {
panelUsers = [panelUsers, <Div><Footer>{i + " место"}</Footer><Cell indicator={<Counter>{"Шанс на победу: " + meUsersGet[i]["winSchance"]}</Counter>} before={<Avatar src={meUsersGet[i]["link"]} />} description={meUsersGet[i]["status"]}>{meUsersGet[i]["name"]}</Cell>  <InfoRow header={"Винрейт: " + meUsersGet[i]["winRate"] + "%"}><Progress value={meUsersGet[i]["winRate"]} /></InfoRow>   </Div>];
}
ReactDOM.render(<App />, document.getElementById("root"));
}

class App extends React.Component {
render() {

  return (
    <View activePanel={activePanel}>
      <Panel id="main">
        <PanelHeader>Меню</PanelHeader>
{panelMain}
      </Panel>
      <Panel id="userMe">
      <PanelHeader left={<PanelHeaderBack onClick={() => {activePanel="main"; ReactDOM.render(<App />, document.getElementById("root"))}}/>}>Статистика</PanelHeader>
{panelUserMe}
      </Panel>
      <Panel id="users">
      <PanelHeader left={<PanelHeaderBack onClick={() => {activePanel="main"; ReactDOM.render(<App />, document.getElementById("root"))}}/>}>Участники</PanelHeader>
{panelUsers}
      </Panel>
    </View>
);}}

ReactDOM.render(<App />, document.getElementById("root"));
